import uniqid from "uniqid";
import Lesson from "./Lesson";


function ScheduleColumn({filter,setFilter,weekday, day, lessons}) {

    //console.log(lessons);

    return (
        <div className="table__column">
            <div/* key={`${index + 2}_1`}*/ className="table__weekday">
                <span>{weekday}</span>
            </div>
            {lessons.length ? lessons.map(lesson => {
                let {lesson_week, day_number, lesson_number, lesson_id} = lesson;
                let key = `${lesson_week}_${day_number}_${lesson_number}_${lesson_id}`;
                let style = {/*gridColumn: `${+day_number}`,*/ gridRow: `${+lesson_number + 1}`,}

                return (<Lesson setFilter={setFilter} filter={filter}  key={uniqid()} style={style}
                                data={lesson}/>);
            }) : <></>
            }
        </div>
    )
}

export default ScheduleColumn;