//React
import {useEffect, useLayoutEffect, useRef, useState} from "react";
import {Link, useLocation} from "react-router-dom";
import uniqid from 'uniqid';

//Moment
import moment from 'moment';
import 'moment/locale/uk';
//Swiper
import {Navigation, Pagination, Scrollbar, A11y, Grid, Controller} from 'swiper';
import {Swiper, SwiperSlide} from 'swiper/react';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import "swiper/css/grid";
import 'swiper/css/scrollbar';
import ScheduleColumn from "./ScheduleColumn";

let lessonTimes = ["8:30", "10:25", "12:20", "14:15", "16:10", "18:30"]
let weeks = ["Перший тиждень", "Другий тиждень"];

function Schedule({id, way}) {
    //Moment locale
    moment.locale('uk')
    let location = useLocation();

    //Get all weekdays with date;
    const startDay = moment().startOf('week').subtract(1, "day");
    const weekdays = [...Array(12)]
        .map(() => startDay.add(1, "day").day() !== 0 ? startDay.format("dd, D") : startDay.add(1, "day").format("dd, D"))
        .map(day => day[0].toUpperCase() + day.slice(1));


    //Schedule/Week state
    const [schedule, setSchedule] = useState({});
    const [week, setWeek] = useState("");
    const [filter, setFilter] = useState("")

    //Fetch Schedule/Week
    useEffect(() => {
        //fetchData function
        async function fetchData() {
            let week = await fetch(`https://api.rozklad.org.ua/v2/weeks`)
                .then(res => res.json())
                .then(json => json.data)
                .then(data => +!(data - 1));
            setWeek(week)

            await fetch(`https://api.rozklad.org.ua/v2/${way}/${id}/lessons`)
                .then(res => res.json())
                .then(json => formatToArray(json.data, week))
                .then(data => {
                    fillEmpty(data);
                    findClosestLesson(data, week)
                    return data;
                }).then(data => setSchedule(data))
                .catch(() => setSchedule([]));
        }

        fetchData();
    }, [location, id])


    function formatToArray(data, week) {
        let result = {};
        data.forEach((lesson) => {
            let {day_number, lesson_week} = lesson;
            lesson.addArticles = "";
            let currentDay = lesson_week - 1 == week ? +day_number : +day_number + 6;
            let lessonsArray = result[currentDay]?.lessons ? [...result[currentDay].lessons, lesson] : [lesson];
            result[currentDay] = {
                day_number: currentDay, week_number: lesson_week - 1, lessons: lessonsArray,
            }
        })

        if (Object.values(result).length < 12) {
            for (let i = 1; i <= 12; i++) {
                if (!result.hasOwnProperty(i)) {
                    result[i] = {
                        day_number: i,
                        week_number: i <= 6 ? week : +!week,
                        lessons: [],
                    }
                }
            }
        }
        return result;
    }

    function findClosestLesson(data, week) {
        let m = moment();
        let day = m.day()
        let isClosestFound = false;
        let days = Object.values(data);

        for (let i = 0; i < days.length && !isClosestFound; i++) {
            let {day_number, week_number, lessons} = days[i];

            if (i == day) m = moment("0:00", "HH:mm");

            if (week_number == week && day_number == day && day != 0) lessons.forEach((lesson) => lesson.addArticles += " today")

            let closest = lessons.find(({
                                            time_start,
                                            time_end
                                        }) => m.isBetween(moment(time_start, "HH:mm:ss"), moment(time_end, "HH:mm:ss")) || m.isBefore(moment(time_start, "HH:mm:ss")))

            //Finish loop
            if (closest) {
                isClosestFound = true;
                closest.addArticles += " closest";
                break;
            }
        }
    }

    function fillEmpty(data) {
        let days = Object.values(data);
        days.forEach((day, index) => {
            let {lessons, day_number, week_number} = day;
            let lessonsObject = lessons.reduce(function (target, key, index) {
                target[key.lesson_number] = key;
                return target;
            }, {})

            for (let i = 1; i < 7; i++) {
                if (!lessonsObject.hasOwnProperty(i)) {
                    lessonsObject[i] = LessonObjectTemplate(i, day_number, week_number, days);
                }
            }
            day.lessons = Object.values(lessonsObject);
        })
    }

    function LessonObjectTemplate(lesson_number, day_number, lesson_week, days) {
        let customLesson = {
            addArticles: "empty",
            day_number,
            lesson_full_name: "",
            lesson_name: "",
            lesson_number,
            lesson_type: "",
            lesson_week,
            rate: "",
            teacher_name: "",
        }
        if (way != "teachers") {
            if (lesson_number == 1) {
                customLesson.addArticles = " white";
                customLesson.lesson_full_name = "Поспи ще трохи 😴";
                customLesson.lesson_name = "Поспи ще трохи 😴";
                customLesson.time_end = "10:05:00";
                customLesson.time_start = "08:30:00";
            }
            if (lesson_number == 1 && !days[day_number - 1].lessons.length) {
                customLesson.addArticles = " white";
                customLesson.lesson_full_name = "Вихідний 🎉";
                customLesson.lesson_name = "Вихідний 🎉";
                customLesson.lesson_number = "1";
                customLesson.time_end = "10:05:00";
                customLesson.time_start = "08:30:00";
            }
            if (lesson_number == 6 && (day_number == 6 || !days[day_number]?.lessons.length)) {
                customLesson.addArticles = "white";
                customLesson.lesson_full_name = "Збирайся на поляну 💃";
                customLesson.lesson_name = "Збирайся на поляну 💃";
                customLesson.time_end = "20:05:00";
                customLesson.time_start = "18:30:00";
            }
        }
        return customLesson;
    }


    //Swiper controller
    /* const swiper1Ref = useRef();
     const swiper2Ref = useRef();
     useLayoutEffect(() => {
         swiper1Ref.current.controller.control = swiper2Ref.current;
         swiper2Ref.current.controller.control = swiper1Ref.current;
     }, []);*/

    // store swiper instance
    const [swiper, setSwiper] = useState(null);
    const [swiperSchedule, setSwiperSchedule] = useState(null);

    const slideTo = (index) => swiper.slideTo(index);
    const slideToSchedule = (index) => swiperSchedule.slideTo(index);

    function changeCurrentSLide(realIndex) {
        if (realIndex + 1 >= 6 && swiper) {
            slideTo(1)
        } else if (swiper) {
            slideTo(0)
        }
    }

    function changeCurrentSLide1(realIndex) {
        if (realIndex == 0) {
            slideToSchedule(0)
        } else if (realIndex == 1) {
            slideToSchedule(5)
        }
    }


    //  console.log(Object.values(schedule))

    let days = schedule;
    // console.log("Now>>", days);
    return (<div className="content__full-schedule">
        <div className="full-schedule__week">
            <Swiper
                onSwiper={setSwiper}
                //onSwiper={swiper => swiper1Ref.current = swiper}
                slidesPerView={1}
                navigation={true}
                modules={[Pagination, Navigation, Controller]}
                onActiveIndexChange={({realIndex}) => {
                    changeCurrentSLide1(realIndex);
                }}>
                <SwiperSlide><span className="week-name">{weeks[week]}</span></SwiperSlide>
                <SwiperSlide><span className="week-name">{weeks[+!week]}</span></SwiperSlide>
            </Swiper>
        </div>
        <div className="full-schedule__table-container">
            <div className="full-schedule__time-column">
                {lessonTimes.map((time, index) => {
                    return (<div key={uniqid()}/*key={`1_${index + 2}`}*/ className="table__lesson-time"
                                 style={{gridColumn: 1, gridRow: `${index + 2}`}}>
                        <span>{time}</span>
                    </div>)
                })}
            </div>
            <div className="full-schedule__table ">
                <Swiper
                    grid={{
                        rows: 1
                    }}

                    initialSlide={0/*moment().day() - 1*/}
                    onSwiper={setSwiperSchedule}
                    slidesPerView={6}
                    spaceBetween={5}
                    modules={[Grid, Pagination, Controller]}
                    onActiveIndexChange={({realIndex}) => {
                        changeCurrentSLide(realIndex);
                    }}
                    breakpoints={{
                        1440: {
                            // width: 1600,
                            slidesPerView: 6
                        }, 1024: {
                            //width: 1024,
                            slidesPerView: 4
                        }, 600: {
                            //width: 1024,
                            slidesPerView: 2
                        }, 0: {
                            //width: 1024,
                            slidesPerView: 1
                        }
                    }}
                >
                    {Object.values(days).map(({day_number, lessons}) => {
                        return (<SwiperSlide><ScheduleColumn
                            filter={filter}
                            setFilter={setFilter}
                            weekday={weekdays[day_number - 1]}
                            day={day_number}
                            lessons={lessons}/></SwiperSlide>)
                    })}
                </Swiper>
            </div>
        </div>
    </div>)
}

export default Schedule;
