import {useEffect, useState} from "react";
import {Link, NavLink, useLocation} from "react-router-dom";

function Header() {

    //Date
    const [date, setDate] = useState(getDate)

    function getDate() {
        return new Date().toLocaleDateString("ua", {
            weekday: "short",
            day: "numeric",
            month: "short",
        }).split(" ").map(e => e[0].toUpperCase() + e.slice(1)).join(" ");
    }

    //Links
    const [links, setLinks] = useState([]);
    let location = useLocation();

    useEffect(() => {
        setLinks([localStorage.getItem("lastSeenGroup"), localStorage.getItem("lastSeenTeacher")])
    }, [location])

    return (
        <header className="header">
            <div className="header__container">
                <span className="header__logo">
                        KPISchedule 🎓
                </span>
                <span className="header__links">
                    <NavLink className="link" activeсlassname='is-active'
                             to={`/groups/${localStorage.getItem("lastSeenGroup")}`}><span
                        className="link-emoji">👨‍🎓</span>  <span className="link-name">Студент</span></NavLink>
                    <span className="links__decoration"/>
                    <NavLink className="link" activeclassname='is-active'
                             to={`/teachers/${localStorage.getItem("lastSeenTeacher")}`}><span
                        className="link-name">Викладач</span> <span className="link-emoji">👨‍🏫</span></NavLink>
                </span>
                <span className="header__date">
                    {date}
                </span>
            </div>
        </header>
    )
}

export default Header;