import {Link} from "react-router-dom";
import uniqid from "uniqid";

function Lesson({style, data, setFilter, filter, key}) {
    let {lesson_name, teachers, lesson_type, addArticles, groups} = data;
    function filterHandler() {
        setFilter(filter === lesson_type ? "" : lesson_type)
    }

    return (
        <div key={key} style={style}
             className={`table__lesson ${addArticles ? addArticles : ""} ${filter && lesson_type !== filter ? "filtered" : ""}`}>
            <div className="lesson__container">
                <span className="lesson__name">{lesson_name}</span>
                {teachers ? <span className="lesson__links">
                    {teachers.map(({teacher_id, teacher_short_name}, index) => teacher_short_name ?
                        <Link key={uniqid()} to={`/teachers/${teacher_id}`}>{teacher_short_name}{index == teachers.length - 1 ? "" : ", "}</Link> : <></>
                    )}</span> : <></>
                }
                {
                    groups ? <span className="lesson__links">
                                    {groups.map(({group_id, group_full_name}, index) => <Link key={uniqid()}
                                                                                              to={`/groups/${group_id}`}>{group_full_name}{index == groups.length - 1 ? "" : ", "}</Link>)}
                                </span> : <></>
                }
                <span className="lesson__type" onClick={() => filterHandler()}>{lesson_type}</span>
            </div>
        </div>
    )
}

export default Lesson;